@media screen and (min-width:857px){
    #root{
        display: flex;
        flex-direction: column;
    }
    #menu{
        margin-top: -1%;
        background-color: white;
        position: sticky;
        top: 0;
        margin-left: -1%;
        width: 101%;
        height: 14%;
        display: flex;
        flex-direction: row;
    }
    #heading{
        margin-left: 1%;
        width: 20%;
    }
    #title{
        background-color: white;
        font-family: yellowtail;
        display: flex;
        flex-direction: row;
        flex-wrap: nowrap;
        font-size: 30px;
        margin-left: 1%;
        margin-right: 0px;
        z-index: 10px;
    }
    #title a {
        color: black;
        text-decoration: none;
    }
    #title a:hover{
        color: crimson;
    }
    #part{
        color: black;
        text-decoration: none;
        margin-right: 5.5%;
    }
    
    #NavC{
        position: absolute;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        font-size: 140%;
        right: 0%;
        box-sizing: border-box;
        padding-top: 2%;
        padding-left: 0.5%;
        font-family: Ropa Sans;
        width: 30%;
    }
    #part:hover{
        border-bottom: 2px solid crimson;
        color: crimson;
    }
    #partend{
        color: black;
        text-decoration: none;
        margin-right: 0px;
    }
    #partend:hover{
        border-bottom: 2px solid crimson;
        color: crimson;
    }
    #second{
        display: flex;
        flex-direction: row;
        width: 100%;
    }
    #container2{
        width: 60%;
        box-sizing: border-box;
        padding-top: 10%;
        padding-left: 20%;
    
    
    }
    #second img{
        width: 40%;
    }
    #container3{
        box-sizing: border-box;
        padding: 2%;
        width: 50%;
        height: 50%;
    }
    #container3 h1{
        font-family: 'Bebas Neue', cursive;
    }
    
    #container3 p {
        font-size: large;
        font-family: yellowtail;
    }
    #partOfMenu{
        display: none;
        
    }
    #follow{
        font-size:medium;
        color: black;
        font-family: 'Bebas Neue', cursive;
        background-color: rgb(0, 238, 255);
        width: 110px;
        height: 40px;
    }
    #follow:hover {
        color: crimson;
    }
    #imagesh{
        display: flex;
        flex-direction:row ;
        flex-wrap: wrap;
    }
    #container img{
        width: 180%;
    }
    #container img:hover{
        z-index: 10px;
        width: 181%;
    }
    
    #container{
        width: 10%;
        margin-right: 9%;
    }
    #data{
        margin-left: 2%;
        padding: 1%;
        font-family: yellowtail;
    }
    #data h1{
        margin-left: 8%;
    }
    #data img{
        border-radius: 10px;
        width: 40%;
    }
    #data img:hover{
        width: 45%;
    }
    #search{
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        padding-top: 2%;
        padding-left: 2%;
        width: 100%;
        height: 600px;
    }
    #inputData{
        width: 10%;
        height: 10%;
        margin-bottom: 2%;
    }
    #search img{
        width: 400px;
        height:400px;
    }
    #footer{
        border-top: 1px solid black;
        display: flex;
        flex-direction: row;
        font-family: 'Fjalla One', sans-serif;
    }
    #footer h2{
        margin-right: 1%;
    }
    
    #footer a {
        text-decoration: none;
        margin-top: 2%;
        margin-right: 1%;
        
    }
}
